import React from 'react'
import { Button } from "@/components/ui/button"
import { Search } from 'lucide-react';
import { Input } from "@/components/ui/input"

function CategorySearch() {
  return (
    <div className='mb-10 items-center flex flex-col'>
        <h2 className='font-bold text-5xl tracking-wide'>Search Doctors</h2>
        <h2 className='text-gray-500 text-2xl'>Search Your Doctor and Book an appointment</h2>
        <div className="flex w-full mt-3 max-w-sm items-center space-x-2">
      <Input type="text" className="py-6 px-5" placeholder="Search doctors here..." />
      <Button type="submit">
        <Search className='h-6 w-6 mr-2' />
      Search</Button>
    </div>
    </div>
  )
}

export default CategorySearch
