import React, { useState } from 'react';
import doctorsData from '../data.json'; // Adjust the path based on your project structure

// Card component for a single doctor
const DoctorCard = ({ doctor }) => {
  return (
    <div className="bg-white shadow-md rounded-md overflow-hidden">
      <img
        src={doctor.image}
        alt={doctor.name}
        className="w-full h-48 object-cover"
      />
      <div className="p-4">
        <h2 className="text-xl font-bold">{doctor.name}</h2>
        <p className="text-gray-600">{doctor.specialization}</p>
        <p className="text-gray-600">{doctor.clinicAddress}</p>
        <p className="text-gray-600 mb-4">{doctor.experience}</p>
        <button className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600">
          Book Appointment
        </button>
      </div>
    </div>
  );
};

const DoctorsPage = () => {
  // Set up pagination state
  const [currentPage, setCurrentPage] = useState(1);
  const doctorsPerPage = 20;
  const totalDoctors = doctorsData.doctors.length;
  const totalPages = Math.ceil(totalDoctors / doctorsPerPage);

  // Calculate which doctors to display for the current page
  const indexOfLastDoctor = currentPage * doctorsPerPage;
  const indexOfFirstDoctor = indexOfLastDoctor - doctorsPerPage;
  const currentDoctors = doctorsData.doctors.slice(indexOfFirstDoctor, indexOfLastDoctor);

  const nextPage = () => {
    if (currentPage < totalPages) setCurrentPage(currentPage + 1);
  };

  const prevPage = () => {
    if (currentPage > 1) setCurrentPage(currentPage - 1);
  };

  return (
    <div className="container mx-auto p-4">
      {/* Display doctor cards in a responsive grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {currentDoctors.map((doctor) => (
          <DoctorCard key={doctor.id} doctor={doctor} />
        ))}
      </div>

      {/* Pagination controls */}
      <div className="flex justify-center items-center mt-4 space-x-4">
        <button
          onClick={prevPage}
          disabled={currentPage === 1}
          className="bg-gray-300 text-gray-700 px-4 py-2 rounded disabled:opacity-50"
        >
          Previous
        </button>
        <span>
          {currentPage} / {totalPages}
        </span>
        <button
          onClick={nextPage}
          disabled={currentPage === totalPages}
          className="bg-gray-300 text-gray-700 px-4 py-2 rounded disabled:opacity-50"
        >
          Next
        </button>
      </div>
    </div>
  );
};

export default DoctorsPage;
